.cvd{
    border: 1px solid rgb(255, 255, 255);
    outline:none;
    width: 55px;
    height: 55px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(255, 255, 255);
    border-radius: 50%;
    position: fixed;
    right: 10px;
    top: 40px;
    z-index: 999;
    flex-direction: column;
}
.cvd h6{
    font-weight: 400;
}
.resume{
    font-size: 20px;
}